<template>
  <div class="providerStore">
    <div class="storebox">
      <div class="storehead">
        <div class="top">
          <div class="left">
            <icon name='fanhui' scale='1.4'></icon>
          </div>
          <div class="right">
            <span>金八宝(新发)</span>
            <span v-show = 'flag'>深圳市福田八宝贸易有限公司</span>
            <div class="servive" v-show = 'flag'>
              <div>
                <icon name='shandian' class="iconn change" scale='0.8'></icon>
                <span>17点前当日送</span>
              </div>
              <div>
                <icon name='feiji' class="iconn change" scale='0.8'></icon>
                <span>200元起送</span>
              </div>
              <div>
                <icon name='huoche' class="iconn change" scale='0.8'></icon>
                <span>免费配送</span>
              </div>
              <div>
                <icon name='baitiao' class="iconn change" scale='0.8'></icon>
                <span>白条支付</span>
              </div>
            </div>
          </div>
        </div>
        <div class="bottom" v-show='flag'>
          <search></search>
        </div>
      </div>
      <div class="goodsList">
        <ul class="left">
          <li>碳酸饮料</li>
          <li class="select">
            <div>
              <span>饮料冲调
                <div class="num"> 36 </div>
              </span>
            </div>
            <div>
              <span>饮用水</span>
            </div>
            <div>
              <span>碳酸饮料</span>
              <div></div>
            </div>
          </li>
          <li>方便速食</li>
        </ul>
        <div class="right">
          <shop-list></shop-list>
          <shop-list></shop-list>
          <div class="botline">
            <div></div>
            <span>已经到底了</span>
            <div></div>
          </div>
        </div>
      </div>
      <div class="shop">
        <go-shopping-cart></go-shopping-cart>
      </div>
    </div>
  </div>
</template>
<script>

import Search from '@/components/Search';
import GoShoppingCart from '@/components/GoShoppingCart';
import ShopList from '@/components/ShopList';

export default {
  components: {
    Search,
    GoShoppingCart,
    ShopList,
  },
  data() {
    return {
      flag: true,
    };
  },
  mounted() {
    const box = this.$el.querySelector('.storebox');
    const storehead = this.$el.querySelector('.storehead');
    const hi = storehead.offsetHeight - 30;
    let mymove = null;
    const mytouch = (e) => {
      const sy = e.touches[0].clientY;
      mymove = (ev) => {
        const my = ev.changedTouches[0].clientY - sy;
        if (my < 0) {
          storehead.style.height = `${hi + my}px`;
          storehead.style.overflow = 'hidden';
        }
      };
      box.addEventListener('touchmove', mymove);
      box.addEventListener('touchend', () => {
        box.removeEventListener('touchmove', mymove);
        this.flag = false;
        storehead.style.height = 'auto';
      });
    };
    box.addEventListener('touchstart', mytouch);
  },
};
</script>

<style lang='scss'>
@import '../styles/index.scss';
.providerStore {
  .storehead {
    background: #708D81;
    padding: 0.3rem 0;
    transition: height 0.2s  linear;
    .top {
      width: 100%;
      display: flex;
      overflow: hidden;
      .left {
        width: 0.85rem;
        color: #fff;
        display: flex;
        @extend .vh-center;
      }
      .right {
        flex-grow: 1;
        height: atuo;
        span {
          display: block;
        }
        span:first-child {
          color: #fff;
          font-size: 0.36rem;
        }
        &>span:nth-child(2) {
          color: $line-color;
          margin: 0.1rem 0 0.2rem 0;
          font-size: 0.3rem;
        }
        .servive {
          color: #fff;
          display: flex;
          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          div {
            height: 0.33rem;
            font-size: 0.2rem;
            color: $line-color;
            margin-right: 0.15rem;
            margin-bottom: 0.2rem;
            display: flex;
            justify-content: space-around;
            align-items: center;
            border: 1px solid $line-color;
            border-radius: 0.18rem;
            padding: 0 0.1rem 0 0;
            .change {
              transform: translate(10%, -10%);
            }
            span {
              font-size: 0.2rem;
              margin-left: 0.02rem;
            }
          }
        }
      }
    }
  }
  .goodsList {
    display: flex;
    margin-bottom: 0.96rem;
    .left {
      width: 1.69rem;
      border-right: 0.01rem solid $line-color;
      background: #F8F8F8;
      li {
        height: 0.85rem;
        text-align: center;
        line-height: 0.85rem;
        font-size: 0.3rem;
        border-bottom: 1px solid $line-color;
      }
      .select {
        height: 2.55rem;
        background: #fff;
        div {
          height: 0.85rem;
        }
        div:nth-child(1) {
          span {
            position: relative;
            width: 1.2rem;
            height: 0.34rem;
            font-size: 0.20rem;
            .num {
              width: 0.36rem;
              height: 0.24rem;
              position: absolute;
              background: $main-color;
              border-radius: 0.105rem;
              color: #fff;
              line-height: 0.26rem;
              text-align: center;
              font-size: 0.1rem;
              top: -0.23rem;
              right: -0.27rem;
            }
          }
        }
        div:nth-child(2) {
          display: flex;
          @extend .vh-center;
          span {
            display: block;
            height: 0.5rem;
            background: $main-color;
            line-height: 0.5rem;
            border-radius: 0.3rem;
            padding: 0 0.1rem;
            color: #fff;
            font-size: 0.2rem;
          }
        }
        div:nth-child(3) {
          display: flex;
          justify-content: space-around;
          align-items: center;
          span {
            color: $none-color;
            font-size: 0.1rem;
            transform: translateX(18%);
          }
          div {
            width: 0.1rem;
            height: 0.1rem;
            background: $main-color;
            border-radius: 50%;
          }
        }
      }
    }
    .right {
      width: 5.8rem;
      .botline {
        display: flex;
        width: 80%;
        margin: 0 auto;
        justify-content: space-around;
        align-items: center;
        height: 0.86rem;
        div {
          width: 30%;
          height: 1px;
          background: $line-color;
        }
        span {
          color: $line-color;
          font-size: 0.2rem;
        }
      }
    }
  }
}
</style>



