  <div class="alllist">
    <div class="listleft">
      <img src="../assets/logo.png" alt="">
    </div>
    <div class="listright">
      <div class="shopname">{{shops.name}}</div>
      <div class="price">
        <div class="priceleft">
          <span>{{shops.unitPrice}}</span>
          <span>{{shops.nowPrice}}</span>
        </div>
        <div class="priceright">
          <div v-if="!shops.buttonFlag" class="buttons">
            <icon name='tianjia' class="iconn" scale='2'></icon>
          </div>
          <div class="nonegoods" v-if="shops.buttonFlag">
            缺货
