          <div v-if="!shops.buttonFlag" class="buttons">
            <icon name='jianshao' class="iconn" scale='1.6'></icon>
            <input type="number" value='6'>
            <icon name='tianjia' class="iconn" scale='1.6'></icon>
          </div>
        </div>
      </div>
      <div class="ordermess" v-show='shops.sellRule.flag'>
        <span>{{shops.sellRule.rule1}}</span>
        <span>{{shops.sellRule.rule2}}</span>
      </div>
    </div>
  </div>
