<style lang='scss'>
@import '../styles/index.scss';

.alllist {
  display: flex;
  width: 100%;
  border-bottom: 1px solid $line-color;
  .listleft {
    width: 35%;
    display: flex;
    @extend .vh-center;
    img {
      width: 80%;
    }
  }
  .listright {
    padding: 0.2rem 0;
    width: 65%;
