            margin: 0.08rem 0;
            color: $none-color;
          }
          &:last-child {
            font-size: 0.36rem;
            color: $main-color;
            margin: 0.1rem 0;
          }
        }
      }
      .priceright {
        display: flex;
        @extend .vh-center;
        width: 50%;
        color: $main-color;
        .buttons {
          display: flex;
          @extend .vh-center;
        }
        .nonegoods{
          color:$none-color;
          font-size:0.28rem;
