        input {
          width: 0.4rem;
          border: none;
          outline: none;
          background: none;
          text-align: center;
          color: $main-color;
        }
      }
    }
    .ordermess {
      span {
        height: 0.34rem;
        border-radius: 0.17rem;
        line-height: 0.36rem;
        padding: 0 0.1rem;
        border: 1px solid #EC6E6D;
        color: #EC6E6D;
        background: #FEF5F5;
        font-size: 0.2rem;
        margin-right: 0.2rem;
