<template>
  <div class="registerVerify">
    <mt-header fixed title="填写验证码" class="head">
      <router-link to="/register" slot="left">
        <icon name="fanhui" scale="1.4" class="iconn"></icon>
      </router-link>
    </mt-header>
    <div class="telm">
      <span>我们已发送验证码到您的手机</span>
      <span>{{123123123}}</span>
      <span>请输入您的手机验证码</span>
      <div class="inputs">
        <input type="number" v-model="verifynum.num1">
        <input type="number" v-model="verifynum.num2">
        <input type="number" v-model="verifynum.num3">
        <input type="number" v-model="verifynum.num4">
      </div>
    </div>
    <mt-button type="primary" class="getagain">
      <span>60</span>s 后重新获取
    </mt-button>
  </div>
</template>
<script>

export default {
  data() {
    return {
      verifynum: {
        num1: '',
        num2: '',
        num3: '',
        num4: '',
      },
    };
  },
  methods: {

  },
  mounted() {
    const inputs = this.$el.querySelectorAll('.inputs input');
    inputs[0].focus();
    let next = 0;
    inputs.forEach((i, j) => {
      inputs[j].addEventListener('input', () => {
        if (next === j) {
          if (inputs[j].value === '') {
            if (next <= 0) {
              inputs[0].focus();
              return;
            }
            next -= 1;
            inputs[next].focus();
          } else {
            if (next === inputs.length - 1) {
              inputs[next].blur();
              const n = this.verifynum.num1 + this.verifynum.num2 + this.verifynum.num3;
              const m = n + this.verifynum.num4;
              console.log(m);
              return;
            }
            next += 1;
            inputs[next].focus();
          }
        }
        inputs.forEach((l, n) => {
          if (n <= next) {
            inputs[n].style.borderBottom = '1px solid #ccac6f';
          } else {
            inputs[n].style.borderBottom = '1px solid #C0C0C0';
          }
        });
      });
    });
  },
};
</script>

<style lang="scss">
@import "../styles/index.scss";
.registerVerify {
  .telm {
    margin-top: 1.2rem;
    font-size: 0.3rem;
    span {
      display: block;
      width: 100%;
      text-align: center;
      color: #C0C0C0;
      margin: 0.36rem 0;
    }
    span:nth-child(2) {
      color: #333;
      font-weight: bold;
    }
    .inputs {
      width: 50%;
      text-align: center;
      display: flex;
      justify-content: space-around;
      margin: 20px auto 10px;
      input {
        width: 0.56rem;
        height: 0.56rem;
        border: none;
        border-bottom: 1px solid #C0C0C0;
        text-align: center;
        outline: none;
        font-weight: bold;
        color: $font-color;
      }
      input:nth-child(1) {
        border-bottom: 1px solid #CCAC6F;
      }
    }
  }
  .getagain {
    @include nextStep(#CCAC6F);
    margin-top: 0.62rem;
  }
}
</style>




