import axios from 'axios';

import hmacsha1 from 'crypto-js/hmac-sha1';
import base64 from 'crypto-js/enc-base64';

const apiHost = process.env.API_HOST;
const apiKey = 'h5';

const instance = axios.create({
  baseURL: apiHost,
  transformRequest: [(data) => {
    const arr = [];
    const { params } = data;
    data.header.uuid = apiKey; // eslint-disable-line no-param-reassign
    Object.keys(params).forEach((key) => {
      const val = params[key];
      arr.push(`${key}=${val}`);
    });
    arr.sort();
    const ksort = arr.join('&');
    const encode = base64.stringify(hmacsha1(`POST&%2F&${ksort}`, apiKey));
    const d = {
      ...data,
      signed: encode,
    };
    return JSON.stringify(d);
  }],
});

const AuthAPI = {
  login: user => instance.post('', { params: { ...user }, header: { api: 'auth.login' } }),
  refresh: params => instance.post('/auth/refresh', params),
  isLogin: params => instance.get('/auth/isLogin', { params }),
};


const GoodsAPI = {
  findById: id => instance.get(`/agoods/${id}`),
  autocomplete: params => instance.get('/agoods/autocomplete', { params }),
  findByName: params => instance.get('/agoods/findByName', { params }),
  findByBarcode: params => instance.get('/auth/findByBarcode', { params }),
};

const WechatAPI = {
  signature: params => instance.get('/wechat/mp/signature', { params }),
};

const RecordAPI = {
  save: data => instance.post('/supplier/record/save', data),
  all: params => instance.get('/supplier/record', { params }),
  total: params => instance.get('/supplier/record/total', { params }),
  category: params => instance.get('/supplier/record/category', { params }),
};

export {
  AuthAPI, WechatAPI, GoodsAPI, RecordAPI,
};



